.head{
    font-size: 50px;
    font-weight: bold;
    margin-top: 10px;
    transition: 6s;
    color: transparent;
    -webkit-text-stroke: 1px #fff;
    background: url(../images/back.png);
    -webkit-background-clip: text;
    background-position: 0 0;
    animation: back 40s linear infinite;
}

@keyframes back {
    100%{
        background-position: 2000px 0;
    }
}


@media (min-width:250px) and (max-width:748px) {
    .head {
        font-size: 25px;
    }
}

@media (min-width:749px) and (max-width:960px) {
    .head {
        font-size: 40px;
    }
}
